import { Component, OnInit, Output, EventEmitter, Input, SimpleChanges } from '@angular/core';
import { GridDataResult } from '@progress/kendo-angular-grid';
import { State, CompositeFilterDescriptor } from '@progress/kendo-data-query';
import { KendoCustomeGrideModel } from '../model/kendoCustomeGrid.model'

const flatten = filter => {
  const filters = filter.filters;
  if (filters) {
    return filters.reduce((acc, curr) => acc.concat(curr.filters ? flatten(curr) : [curr]), []);
  }
  return [];
};

@Component({
  selector: 'app-kendo-custome-grid',
  templateUrl: './kendo-custome-grid.component.html',
  styleUrls: ['./kendo-custome-grid.component.scss']
})
export class KendoCustomeGridComponent implements OnInit {


  @Input() GridColumns: any[] = [];
  @Input() gridView: GridDataResult = { data: [], total: 3 };
  @Input() loading: boolean = false;
  @Input() filterable: boolean = true;
  @Input() pageable: boolean = true;
  @Input() actionColumn: boolean = true;
  @Input() sortable: any = { allowUnsort: true, mode: 'multiple' }
  @Input() grideRender: boolean = false;
  @Input() reorderable: boolean = false;
  @Input() footers: any = {};
  @Input() resizable = false;
  @Input() state: State = {
    skip: 0,
    take: 15,
  };
  @Input() autoCompleteData: { name: string, list: any[] } | [{ name: string, list: any[] }] | any;
  taxIdMask: string = "00-0000000";
  taxIdTimeOut: any;


  public filter: CompositeFilterDescriptor;
  public checkLockColumn: boolean = false;
  private timeout: any;

  @Output() stateChange: EventEmitter<State> = new EventEmitter();
  @Output() cellClick: EventEmitter<any> = new EventEmitter();
  @Output() commandActionClick: EventEmitter<any> = new EventEmitter();
  @Output() autoCompleteFilterChange: EventEmitter<any> = new EventEmitter();

  constructor() { }

  ngOnInit(): void {
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.GridColumns && changes.GridColumns.currentValue.length > 0) {
      this.grideRender = true;
      this.GridColumns.filter(ele => { return (ele.locked && !ele.hidden) }).length > 0 ? this.checkLockColumn = true : this.checkLockColumn = false;
    }
  }

  cellOnClick(dataItem: any, column: any) {
    let event = {
      data: dataItem,
      column: column
    }
    this.cellClick.emit(event);
  }

  dataStateChange(event: State) {
    this.state = event;
    this.stateChange.emit(event);
  }

  adjustFilter() {
    let filterArray = JSON.parse(JSON.stringify(this.state.filter['filters']));

    filterArray.find((filter, index) => {

      if (filter['field'].includes('date') || filter['field'].includes('Date')) {
        filter['value'] = new Date(filter['value']).getTime();
        filterArray[index] = filter;
      }
    })
    return filterArray;
  }

  checkHiddenCondition(show: boolean, hide: boolean) {
    show = show === false ? false : true;
    if (show && !hide) {
      return false
    } else {
      return true
    }
  }

  commandAction(action: any, column: any, data: any) {
    let event = {
      action: action,
      column: column,
      data: data
    }
    this.commandActionClick.emit(event);
  }

  AutoCompleteFilterChangeCheck(event: any) {
    clearTimeout(this.timeout);
    this.timeout = setTimeout(() => {
      this.autoCompleteFilterChange.emit(event);
    }, 800);

  }

  isArray(data: any) {
    return Array.isArray(data);
  }

  IsSome(array: any[], someAttr: any, value: any): { check: boolean, index: number } {

    let values = {
      check: array.some(ele => ele[someAttr] === value),
      index: array.findIndex(ele => ele[someAttr] === value)
    }
    return values;
  }
}
